<template>
    <div class="invoiceRecord_container">
        <head-top head-title="购买记录" goBack="true"></head-top>
        <div class="invoiceRecord_center">
            <div class="invoiceRecord_img">
                <img src="../../../images/no-log.png">
                <p>没有购买记录</p> 
            </div>
        </div>
    </div>
</template>

<script>
import headTop from '../../../components/header/header.vue'
export default {
    components: {
        headTop
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.invoiceRecord_container{
    @include zi;
    padding-top: 1.95rem;
}
.invoiceRecord_center{
    .invoiceRecord_img{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        p{
            @include sc(0.6rem,#333);
            padding-top: 0.3rem;
        }
    }
}
</style>



